import { Navbar, Nav, Container, ListGroup } from 'react-bootstrap';
import { Link, Outlet } from 'react-router-dom';
import Balance from './Balance';

function Sidebar({changeColour}) {
  return <nav className='navigation bg-secondary'>
  <ListGroup variant='flush'>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/addIncomes"}> </Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/history"}> </Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/statistic"}> </Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3' to={"/options"}></Link>
    </ListGroup.Item>
    <ListGroup.Item variant='dark' className='p-0' onClick={changeColour}>
      <Link className='sidebar-item nav-link text-dark p-3'></Link>
    </ListGroup.Item>
  </ListGroup>
  <Balance/>
</nav>
}

export default Sidebar